<template>
	<div class="box">
		<layout-box>
			<box-item title="区域分布" :loading="loading">
				<div class="area">
					<div class="module-body">
						<div class="echart-container" ref="EContainer" v-show="hasData"></div>
						<div class="no-data" v-show="!hasData && !loading"><span>暂无数据</span></div>
					</div>
				</div>
			</box-item>
		</layout-box>
	</div>
</template>
<script>
import LayoutBox from '@/components/common/LayoutBox.vue';
import BoxItem from '@/components/common/BoxItem.vue';
const barOption = {
	grid: {
		show: true,
		borderColor: '#0D394A',
		top: 40,
		bottom: 56,
		left: 45,
		right: 20
	},
	tooltip: {
		show: true,
		trigger: 'item',
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		}
		// formatter(a) {
		// 	return `${a.name}：${a.value}万亩`;
		// }
	},
	xAxis: {
		data: [],
		axisLabel: {
			interval: 0,
			textStyle: {
				color: '#6EC2CB',
				fontSize: 12,
				lineHeight: 14
			},
			align: 'center'
			// rotate: -40,
			// margin: 25,
			// formatter(name) {
			// 	return name.split('').join('\n');
			// }
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: true,
			lineStyle: {
				color: '#32CEBB'
			}
		}
	},
	yAxis: {
		name: '品牌数',
		nameTextStyle: {
			fontSize: 12,
			color: '#6DC1CB',
			padding: [0, 0, 3, 0]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	series: [
		{
			type: 'bar',
			barWidth: '40%',
			barMinHeight: 5,
			data: [],

			label: {
				show: true,
				position: 'top',
				color: '#00F6FF',
				fontSize: 12
			},
			z: 10,
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#00F6FF' // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#01C1FF' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			}
		}
	]
};
export default {
	props: {
		data: {
			type: Array,
			default: () => []
		},
		loading: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			chartInstance: null
		};
	},
	created() {},
	watch: {
		// data变化重新渲染图表
		data: function(newData) {
			this.$nextTick(() => {
				this.refreshChart(newData);
			});
		}
	},
	computed: {
		hasData() {
			return this.data.length > 0;
		},
		eContainerRef() {
			return this.$refs.EContainer;
		}
	},
	mounted() {
		this.$nextTick(() => {
			this.initEcharts(this.$refs.EContainer, barOption);
		});
	},
	methods: {
		initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			this.chartInstance = chart;
			chart.setOption(option);
		},
		refreshChart(data) {
			const chartOpts = {
				xAxis: {
					data: data.map(t => t.name)
				},
				series: [
					{
						data: data.map(t => t.count)
					}
				]
			};
			this.chartInstance.setOption(chartOpts);
			this.chartInstance.resize();
		}
	},
	components: {
		LayoutBox,
		BoxItem
	}
};
</script>
<style scoped lang="scss">
.box {
	box-sizing: border-box;
	padding: 10px;
	height: 50%;
}
.area {
	position: relative;
	height: 100%;
	.module-body {
		width: 100%;
		height: 100%;
		.echart-container {
			width: 100%;
			height: 100%;
		}
		.no-data {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 14px;
			color: #00f6ff;
			font-weight: bold;
		}
	}
}
</style>



